export default {
  data () {
    return {
      page: {},
      isPageFetched: false
    }
  },
  created () {
    this.fetchPage()
  },
  methods: {
    fetchPage () {
      var title = this.$route.params.title

      this.$http.get(`/page/${title}`).then((response) => {
        var data = response.data

        if (data.success === true) {
          this.page = data.msg
          this.isPageFetched = true
          this.$emit('updateBreadcrumb', [{
            obj: {
              name: 'page',
              params: {
                title: title
              }
            },
            content: title
          }])
        } else {
          this.$emit('handleError', true)
        }
      }, (error) => {
        this.$emit('handleError', true)
        throw error
      })
    }
  },
  watch: {
    '$route': 'fetchPage'
  }
}



